// Use installed dependencies
import * as THREE from 'three';
import { OrbitControls } from 'three-stdlib/controls/OrbitControls.js';
import { FontLoader } from 'three-stdlib/loaders/FontLoader.js';
import { TextGeometry } from 'three-stdlib/geometries/TextGeometry.js';

// Scene setup
const scene = new THREE.Scene();
scene.background = new THREE.Color(0xf0f0f0);

const camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 150);
camera.position.set(-15, 10, 15);

const renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

const controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true;

// Lighting
const ambientLight = new THREE.AmbientLight(0xffffff, 0.8);
scene.add(ambientLight);

const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6);
directionalLight.position.set(5, 5, 10);
scene.add(directionalLight);

// Floor
const floorGeometry = new THREE.PlaneGeometry(80, 60);
const floorMaterial = new THREE.MeshStandardMaterial({ color: 0xaaaaaa });
const floor = new THREE.Mesh(floorGeometry, floorMaterial);
floor.rotation.x = -Math.PI / 2;
scene.add(floor);

// Materials
const columnMaterial = new THREE.MeshStandardMaterial({ color: 0xffffff });

// Adjusted sizes
const wallHeight = 3.2; // Chiu cao tng 3.2 mt
const columnHeight = 3.2; // Chiu cao ct 3.2 mt
const boardWidth = 2.4; // Tm bng rng 240cm
const boardHeight = 1.8; // Tm bng cao 180cm

// Tng gia (ni hai ct)
const centerWall = new THREE.Mesh(new THREE.BoxGeometry(11, wallHeight, 0.2), columnMaterial);
centerWall.position.set(0, wallHeight / 2, -2.5);
scene.add(centerWall);

// Ct hnh ch nht bn tri
const leftRectColumn = new THREE.Mesh(new THREE.BoxGeometry(0.75, columnHeight, 0.6), columnMaterial);
leftRectColumn.position.set(-5.5, columnHeight / 2, -2.5);
scene.add(leftRectColumn);

// Ct hnh ch nht bn phi
const rightRectColumn = new THREE.Mesh(new THREE.BoxGeometry(0.75, columnHeight, 0.6), columnMaterial);
rightRectColumn.position.set(5.5, columnHeight / 2, -2.5);
scene.add(rightRectColumn);

// Ct hnh trn bn tri
const leftColumn = new THREE.Mesh(new THREE.CylinderGeometry(0.5, 0.5, columnHeight, 32), columnMaterial);
leftColumn.position.set(-5.5, columnHeight / 2, 1);
scene.add(leftColumn);

// Ct hnh trn bn phi
const rightColumn = new THREE.Mesh(new THREE.CylinderGeometry(0.5, 0.5, columnHeight, 32), columnMaterial);
rightColumn.position.set(5.5, columnHeight / 2, 1);
scene.add(rightColumn);

// Bng
const boardMaterial = new THREE.MeshStandardMaterial({ color: 0x87CEFA });
const board = new THREE.Mesh(new THREE.BoxGeometry(boardWidth, boardHeight, 0.1), boardMaterial);
board.position.set(-2, 1.7, -2.4);
scene.add(board);

// Bc tng mi st ct ch nht phi
const rightWall = new THREE.Mesh(new THREE.BoxGeometry(11, wallHeight, 0.2), columnMaterial);
rightWall.position.set(11, wallHeight / 2, -2.5);
scene.add(rightWall);

// Ct hnh ch nht bn phi (st tng mi)
const newRightRectColumn = new THREE.Mesh(new THREE.BoxGeometry(0.75, columnHeight, 0.6), columnMaterial);
newRightRectColumn.position.set(16.5, columnHeight / 2, -2.5);
scene.add(newRightRectColumn);

// Ct hnh trn bn phi (st tng mi)
const newRightColumn = new THREE.Mesh(new THREE.CylinderGeometry(0.5, 0.5, columnHeight, 32), columnMaterial);
newRightColumn.position.set(16.5, columnHeight / 2, 1);
scene.add(newRightColumn);

// Bn
const tableMaterial = new THREE.MeshStandardMaterial({ color: 0x8B4513 }); // Mu g
const tableGeometry = new THREE.BoxGeometry(2.0, 0.3 , 0.6); // Chiu di, cao, rng
const table = new THREE.Mesh(tableGeometry, tableMaterial);
table.position.set(2, 0.75, -1.9);
scene.add(table);

// Chn bn (4 chn hnh ct)
const legMaterial = new THREE.MeshStandardMaterial({ color: 0x8B4513 });
const legGeometry = new THREE.CylinderGeometry(0.05, 0.05, 0.75, 32); // Hnh tr: ng knh 0.05, cao 0.75
const cylinderLegs = [
    new THREE.Mesh(legGeometry, legMaterial), // Chn 1
    new THREE.Mesh(legGeometry, legMaterial), // Chn 2
    new THREE.Mesh(legGeometry, legMaterial), // Chn 3
    new THREE.Mesh(legGeometry, legMaterial)  // Chn 4
];

// Tnh ton v tr cc chn da trn v tr mi ca mt bn
const tableLength = 2.0; // Chiu di mt bn
const tableWidth = 0.6;  // Chiu rng mt bn
const offsetX = tableLength / 2 - 0.1; // V tr chn dc trc X (t mp vo trong)
const offsetZ = tableWidth / 2 - 0.1;  // V tr chn dc trc Z (t mp vo trong)
const tableY = 0.75 / 2;               //  cao ca chn tnh t gia mt bn

// t v tr mi cho tng chn
cylinderLegs[0].position.set(2 - offsetX, tableY, -1.9 + offsetZ);  // Chn 1: gc trn tri
cylinderLegs[1].position.set(2 - offsetX, tableY, -1.9 - offsetZ);  // Chn 2: gc di tri
cylinderLegs[2].position.set(2 + offsetX, tableY, -1.9 + offsetZ);  // Chn 3: gc trn phi
cylinderLegs[3].position.set(2 + offsetX, tableY, -1.9 - offsetZ);  // Chn 4: gc di phi
// Thm cc chn bn vo scene
cylinderLegs.forEach((leg) => scene.add(leg));

// Cnh ca v khung ca
const doorMaterial = new THREE.MeshStandardMaterial({ color: 0x000000 });
const doorGeometry = new THREE.BoxGeometry(1.2, 2.2, 0.05);
const door = new THREE.Mesh(doorGeometry, doorMaterial);
door.position.set(10.0, 1.1, -2.4);
scene.add(door);

const doorFrameMaterial = new THREE.MeshStandardMaterial({ color: 0x000000 });
const doorFrameGeometry = new THREE.BoxGeometry(1.3, 2.4, 0.05);
const doorFrame = new THREE.Mesh(doorFrameGeometry, doorFrameMaterial);
doorFrame.position.set(10.0, 1.2, -2.4);
scene.add(doorFrame);

// Bng mu en (Bng 2) vi khung treo ch L
const blackBoardMaterial = new THREE.MeshStandardMaterial({ color: 0x000000 }); // Mu en
const blackBoard = new THREE.Mesh(
    new THREE.BoxGeometry(0.1, 1.8, 1.8), // Kch thc bng: 1.8 x 1.8
    blackBoardMaterial
);

// V tr t bng:
// t  gia ct ch nht v ct trn, bn di thanh ngang
blackBoard.position.set(16.0, 1.9, -1.0); 
scene.add(blackBoard);

// Khung treo ch L (Bng 2)
const boardFrameMaterial = new THREE.MeshStandardMaterial({ color: 0x333333 }); // Mu xm

// Ct ng bn phi ca khung ch L
const verticalPoleRight = new THREE.Mesh(
  new THREE.BoxGeometry(0.1, 3.0, 0.1), // Ct ng: cao 3m, tit din vung 0.1
  boardFrameMaterial
);
verticalPoleRight.position.set(16.0, 1.5, -1.85); // t st pha sau bng en (bn phi)
scene.add(verticalPoleRight);

// Ct ng bn tri ca khung ch L
const verticalPoleLeft = new THREE.Mesh(
  new THREE.BoxGeometry(0.1, 3.0, 0.1), // Ct ng: cao 3m, tit din vung 0.1
  boardFrameMaterial
);
verticalPoleLeft.position.set(16.0, 1.5, -0.15); // t st pha sau bng en (bn tri)
scene.add(verticalPoleLeft);

// Thanh ngang gn trn nh ct ng
const horizontalPole = new THREE.Mesh(
    new THREE.BoxGeometry(0.1, 0.1, 1.8), // Thanh ngang: di 1.8m, tit din vung 0.1
    boardFrameMaterial
);
horizontalPole.position.set(16.0, 3.0, -1.0); // t trn nh ct ng, gi nguyn thng s
scene.add(horizontalPole);

// Gn bng en (Bng 2) vo thanh ngang
blackBoard.position.y = 2.1; // iu chnh bng en  gn ngay bn di thanh ngang

// Bn mi (bn 2) - Cng hng vi bng 2
const newTable = new THREE.Mesh(tableGeometry, tableMaterial);
newTable.position.set(15, 0.75, 0); // V tr gn bng 2
newTable.rotation.y = Math.PI / 2; // Xoay bn 90  quanh trc Y
scene.add(newTable);

// Chn bn mi (4 chn hnh ct)
const newCylinderLegs = [
    new THREE.Mesh(legGeometry, legMaterial), // Chn 1
    new THREE.Mesh(legGeometry, legMaterial), // Chn 2
    new THREE.Mesh(legGeometry, legMaterial), // Chn 3
    new THREE.Mesh(legGeometry, legMaterial)  // Chn 4
];

// V tr cc chn ca bn mi
newCylinderLegs[0].position.set(15 - offsetZ, tableY, 0 - offsetX);  // Chn 1: gc trn tri
newCylinderLegs[1].position.set(15 + offsetZ, tableY, 0 - offsetX);  // Chn 2: gc di tri
newCylinderLegs[2].position.set(15 - offsetZ, tableY, 0 + offsetX);  // Chn 3: gc trn phi
newCylinderLegs[3].position.set(15 + offsetZ, tableY, 0 + offsetX);  // Chn 4: gc di phi

// Thm cc chn bn mi vo scene
newCylinderLegs.forEach((leg) => scene.add(leg));

// Render loop
const animate = () => {
    controls.update();
    renderer.render(scene, camera);
    requestAnimationFrame(animate);
};
animate();